body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  color: #000000;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  align-items: center;
  padding: 10px;
}

h1 {
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  margin: 10px 0 5px 0;
  letter-spacing: 0.2em;
  color: #000000;
  border-bottom: 1px solid #d3d6da;
  padding-bottom: 10px;
  width: 100%;
  max-width: 350px;
}

/* Author credit styling */
.author-credit {
  font-size: 0.8rem;
  color: #787c7e;
  text-align: center;
  margin: 5px 0 15px 0;
  font-weight: 400;
  letter-spacing: 0.1em;
}

/* Chances remaining counter */
.chances-remaining {
  font-size: 1rem;
  color: #565758;
  text-align: center;
  margin: 10px 0;
  font-weight: 500;
}

/* Game over messages styling */
.game-lost {
  text-align: center;
  color: #d73027;
  font-size: 1.5rem;
  margin: 10px 0;
  font-weight: bold;
  animation: shake 0.6s ease;
}

.game-won {
  text-align: center;
  color: #6aaa64;
  font-size: 1.5rem;
  margin: 10px 0;
  font-weight: bold;
  animation: celebrate 0.6s ease;
}

input {
  width: 280px;
  padding: 12px;
  font-size: 16px;
  text-align: center;
  border: 2px solid #d3d6da;
  border-radius: 4px;
  margin: 15px 0 8px 0;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: bold;
  outline: none;
  transition: border-color 0.2s ease;
}

input:focus {
  border-color: #787c7e;
}

input:disabled {
  background-color: #f6f7f8;
  color: #787c7e;
}

button {
  width: 280px;
  padding: 12px;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
  background-color: #6aaa64;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s ease;
  letter-spacing: 0.1em;
}

button:hover:not(:disabled) {
  background-color: #5a9a54;
}

button:disabled {
  background-color: #d3d6da;
  cursor: not-allowed;
}

/* Game board container */
div > div[style*="display: flex"] {
  margin: 20px 0 15px 0;
}

/* Individual guess rows */
div > div[style*="display: flex"][style*="gap: 5px"] {
  display: flex !important;
  gap: 6px !important;
  margin: 6px 0 !important;
  justify-content: center;
}

/* Letter tiles */
span[style*="display: inline-block"] {
  display: inline-block !important;
  width: 50px !important;
  height: 50px !important;
  line-height: 50px !important;
  text-align: center !important;
  font-weight: bold !important;
  border-radius: 4px !important;
  font-size: 20px !important;
  text-transform: uppercase !important;
  border: 2px solid transparent !important;
  transition: all 0.3s ease !important;
  animation: flipIn 0.6s ease forwards !important;
}

/* Color overrides for the tiles */
span[style*="background: green"] {
  background-color: #6aaa64 !important;
  border-color: #6aaa64 !important;
  color: white !important;
}

span[style*="background: yellow"] {
  background-color: #c9b458 !important;
  border-color: #c9b458 !important;
  color: white !important;
}

span[style*="background: gray"] {
  background-color: #787c7e !important;
  border-color: #787c7e !important;
  color: white !important;
}

/* Congratulations message */
h2 {
  text-align: center;
  color: #6aaa64;
  font-size: 1.5rem;
  margin: 10px 0;
  animation: celebrate 0.6s ease;
}

/* Animations */
@keyframes flipIn {
  0% {
    transform: rotateX(-90deg);
  }
  50% {
    transform: rotateX(0deg) scale(1.1);
  }
  100% {
    transform: rotateX(0deg) scale(1);
  }
}

@keyframes celebrate {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

@keyframes shake {
  0%, 100% { transform: translateX(0); }
  10%, 30%, 50%, 70%, 90% { transform: translateX(-5px); }
  20%, 40%, 60%, 80% { transform: translateX(5px); }
}

/* Responsive design */
@media (max-width: 768px) {
  #root {
    padding: 15px;
  }
  
  h1 {
    font-size: 2.2rem;
    margin: 15px 0 30px 0;
    max-width: 300px;
  }
  
  input, button {
    width: 90%;
    max-width: 280px;
    min-width: 250px;
  }
  
  span[style*="display: inline-block"] {
    width: 50px !important;
    height: 50px !important;
    line-height: 50px !important;
    font-size: 20px !important;
  }
  
  div > div[style*="display: flex"][style*="gap: 5px"] {
    gap: 6px !important;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 1.8rem;
    letter-spacing: 0.1em;
    max-width: 250px;
  }
  
  input, button {
    width: 95%;
    max-width: 260px;
    min-width: 200px;
    padding: 12px;
    font-size: 16px;
  }
  
  span[style*="display: inline-block"] {
    width: 45px !important;
    height: 45px !important;
    line-height: 45px !important;
    font-size: 18px !important;
  }
  
  div > div[style*="display: flex"][style*="gap: 5px"] {
    gap: 4px !important;
  }
  
  h2 {
    font-size: 1.5rem;
  }
}

@media (max-width: 360px) {
  h1 {
    font-size: 1.6rem;
  }
  
  input, button {
    padding: 10px;
    font-size: 14px;
  }
  
  span[style*="display: inline-block"] {
    width: 40px !important;
    height: 40px !important;
    line-height: 40px !important;
    font-size: 16px !important;
  }
  
  div > div[style*="display: flex"][style*="gap: 5px"] {
    gap: 3px !important;
  }
}

/* Large screens */
@media (min-width: 1200px) {
  h1 {
    font-size: 3rem;
  }
  
  span[style*="display: inline-block"] {
    width: 70px !important;
    height: 70px !important;
    line-height: 70px !important;
    font-size: 28px !important;
  }
  
  div > div[style*="display: flex"][style*="gap: 5px"] {
    gap: 10px !important;
  }
  
  input, button {
    width: 350px;
    padding: 18px;
    font-size: 20px;
  }
}

/* Add subtle shadow to the game container */
div {
  filter: drop-shadow(0 4px 6px rgba(0, 0, 0, 0.1));
}